import React from 'react';
//import Part from './../scripts/part';
import SimpleReturnComponent from './simpleReturnComponent'
import Selector from './selector.js'
import SelectedPointOnEdge from './selectedPointOnEdge.js';
import SelectedPointOnPath from './selectedPointOnPath.js';
import SelectedEdge from './selectedEdge.js';
import Guides from './guides.js'
import svgStore from './stores/svgStore.js';
import { observer } from 'mobx-react-lite';
import Joints from './joints.js';
import HighLighted from './highlighted.js';
import LaserShow from './laserShow.js';
import util from '../utils/util.js';


const SvgComponent = observer (() => {


    const {
        matrix,  
        groupMatrix,
        rectParams,
        gridState,
        svgParams,
        

    } = svgStore
   

    const touchZoom = (event, curDiff, prevDiff) =>{
		console.log ('** touchZoom **')
		var svg = document.getElementById("svg")
        // Коэффициент масштабирования на основе разницы расстояний
        let scale = curDiff / prevDiff;
		var group = document.getElementById("group").transform.baseVal.consolidate().matrix

        // Вычисление координат центра между двумя точками касания
        let x = (evCache[0].clientX + evCache[1].clientX) / 2;
        let y = (evCache[0].clientY + evCache[1].clientY) / 2;
        let coords = util.convertScreenCoordsToSvgCoords(x, y);
    
        // Применение трансформации
		var gTransform = svg.createSVGMatrix()
        gTransform = gTransform.translate(coords.x, coords.y);
        gTransform = gTransform.scale(scale, scale);
        gTransform = gTransform.translate(-coords.x, -coords.y);
		let comboMatrix = util.multiplyMatrices(group, gTransform)
		
		svgStore.setMatrix({
			a: comboMatrix.a,
			b: comboMatrix.b,
			c: comboMatrix.c,
			d: comboMatrix.d,
			e: comboMatrix.e,
			f: comboMatrix.f
		});

        let attr = calculateRectAttributes()
		svgStore.setRectParams( attr)
    
     	/*Применение трансформации к элементу SVG
        let transform = part.svg.createSVGTransform();
        transform.setMatrix(part.gTransform);
        part.group.transform.baseVal.initialize(transform);
        */
    }

    const calculateRectAttributes = () => {
		//debugger
		const widthSVG = svgParams.width
		const heightSVG = svgParams.height
	
        // Ширина и высота исходя из scale
        const combinedMatrix = util.multiplyMatrices(groupMatrix, matrix);
        const scaleX = combinedMatrix.a;
        const scaleY = combinedMatrix.d;

        const width = widthSVG/ scaleX;
        const height = heightSVG / scaleY;

        // Координаты x и y исходя из translate
        const x = -combinedMatrix.e / scaleX;
        const y = -combinedMatrix.f / scaleY;

        return { x:x, y:y, width:width, height:height }
    };
 
    let evCache = []; // Список активных касаний
    let prevDiff = -1; // Предыдущее расстояние между пальцами
    
    // Обработчик начала касания
    const handleTouchStart =(event)=> {
        console.log ('handleTouchStart')
        evCache = []; // Список активных касаний
        prevDiff = -1; 
        //event.preventDefault()
        evCache.push(event);
   
    }
    
    // Обработчик движения пальцев
    const handleTouchMove =(event)=> {
        console.log ('handleTouchMove')
        //event.preventDefault()


        for (let i = 0; i < event.touches.length; i++) {
            evCache[i] = event.touches[i];
        }
    
        if (evCache.length === 2) {
            let curDiff = getDistance(evCache[0], evCache[1]);
    
            if (prevDiff > 0) {
                touchZoom(event, curDiff, prevDiff); // Вызов функции масштабирования
            }
            prevDiff = curDiff;
        }
    }
    
    // Обработчик окончания касания
    const handleTouchEnd =(event) =>{
        //event.preventDefault()
        console.log ('handleTouchEnd')

        for (let i = 0; i < event.changedTouches.length; i++) {
            let index = evCache.findIndex(t => t.identifier === event.changedTouches[i].identifier);
            if (index > -1) evCache.splice(index, 1);
        }
    
        // Если осталось одно или ноль касаний, сбрасываем prevDiff
        if (evCache.length < 2) {
            prevDiff = -1;
        }
    }
    
    // Функция вычисления расстояния между двумя точками касания
    const getDistance = (touch1, touch2)=> {
        let dx = touch1.clientX - touch2.clientX;
        let dy = touch1.clientY - touch2.clientY;
        return Math.sqrt(dx * dx + dy * dy);
    }


    const matrixM = `${matrix.a} ${matrix.b} ${matrix.c} ${matrix.d} ${matrix.e} ${matrix.f}`;
    const matrixG = `${groupMatrix.a} ${groupMatrix.b} ${groupMatrix.c} ${groupMatrix.d} ${groupMatrix.e} ${groupMatrix.f}`;
	const widthSVG = svgParams.width
	const heightSVG = svgParams.height

    return (
        <svg
            id="svg"
            baseProfile="full"
            viewBox={`0.00 0.00 ${widthSVG} ${heightSVG}`}
            style={{ overflow: 'hidden', border: '1px solid var(--color)' }}
            version="1.1"
            stroke='var(--color)'
            strokeWidth="0.2"
        >
            <defs>
                <pattern  
                    id="xsGrid" 
                    width="1" 
                    height="1" 
                    fill="var(--gridColorFill)" 
                    patternUnits="userSpaceOnUse" 
                    visibility={gridState.xsGrid.visibility}>
                    <path  d="M 0 0 1 0 1 1 0 1 0 0" fill="var(--gridColorFill)" stroke="var(--gridColorStroke)" strokeWidth="0.05"/>
                </pattern>
                <pattern 
                    id="smallGrid" 
                    width="10" 
                    height="10" 
                    patternUnits="userSpaceOnUse" >
                    <rect  width="100" height="100" fill="url(#xsGrid)"/>
                    <path  
                    d="M 10 0 L 0 0 0 10 10 10 10 0" 
                    fill={gridState.smallGrid.fill} 
                    stroke="var(--gridColorStroke)" 
                    strokeWidth="0.1" 
                    visibility={gridState.smallGrid.visibility}/>
                </pattern>
                <pattern  
                    id="grid" 
                    width="100" 
                    height="100" 
                    patternUnits="userSpaceOnUse"
                    visibility={gridState.grid.visibility}>
                    <rect  
                    width="100" height="100" 
                    fill="url(#smallGrid)"/>
                    <path  
                    d="M 100 0 0 0 0 100 100 100 100 0" 
                    fill={gridState.grid.fill} 
                    stroke="var(--gridColorStroke)" 
                    strokeWidth="0.2"/>
                </pattern>
                <marker id="dotRed" viewBox="0 0 10 10" refX="5" refY="5" markerWidth="5" markerHeight="5">
                    <circle 
                    cx="5" 
                    cy="5" 
                    r="5" 
                    fill="red"></circle>
                </marker>
                <marker id="dotPink" viewBox="0 0 10 10" refX="5" refY="5" markerWidth="5" markerHeight="5">
                    <circle 
                    cx="5" 
                    cy="5" 
                    r="5" 
                    fill="pink"></circle>
                </marker>
                <marker id="dotYellow" viewBox="0 0 10 10" refX="5" refY="5" markerWidth="5" markerHeight="5">
                    <circle 
                    cx="5" 
                    cy="5" 
                    r="5" 
                    fill="yellow"></circle>
                </marker>                
            </defs>
            <g id="group2" fill="url(#grid)"
                
                onTouchStart = {handleTouchStart}
                onTouchMove = {handleTouchMove}
                onTouchEnd = {handleTouchEnd}
                onTouchCancel = {handleTouchEnd}
            >
                <g id="group1" transform={`matrix(${matrixG})`}>
                    <g id="group" transform={`matrix(${matrixM})`} className="grab">
                        <g id="contours">
                            <rect
                                id="dimensionalGrid"
                                height={rectParams.height}
                                width={rectParams.width}
                                x={rectParams.x}
                                y={rectParams.y}
                                fill="url(#grid)"
                                transform-origin="50% 50%"
                                stroke='var(--color)'
                                strokeWidth="0"
                            ></rect>
                            <>
                             < SimpleReturnComponent />
                            </>
                       </g>
                       <Selector />
                       <Guides />
                       <SelectedPointOnEdge/>
                       <Joints />
                       <SelectedPointOnPath/>
                       <SelectedEdge/>
                       <HighLighted />
                       <LaserShow />
                    </g>
                </g>
            </g>
        </svg>
    );
});

export default SvgComponent;